
import React from "react";
import { BrowserRouter as Router, Link, Switch, Route } from "react-router-dom";
import Navbar from "react-bootstrap/Navbar";
import { FaMailBulk, FaPhoneAlt } from 'react-icons/fa';
import '../App.css';


function AboutUs() {
    return (
        <div class="container">
            <h2 className="text-center mb-3">We are here to help you!</h2>
            <div><p>What is Lorem Ipsum Lorem Ipsum is simply dummy text of the printing and
            typesetting industry Lorem Ipsum has been the industry's standard dummy text ever
            since the 1500s when an unknown printer took a galley of type and scrambled it to make
       a type specimen book it has?</p></div>
        </div>
    );
}

function ContactUs() {
    return (
        <div class="container">
            <div class=" mt-5">
                <div className="contactUs">
                    <p> <FaMailBulk /> :abc@gmail.com</p>
                    <p> <FaPhoneAlt />:1234567898</p>
                </div>
            </div>
        </div>

    );
}
function HomePage() {
    return (
        <div className="homepage"><h1 style={{ color: "brown" }}>Welcome to see our journey!</h1>
            <h2 style={{ color: 'cadetblue' }}>EXPLORE IN BUDGET!</h2>
            <p>This is the home page.</p>
        </div>

    );
}

function NotFound() {
    return (
        <div className="container bg-light">
            <h1 className="m-5">404 Page not found</h1>
            <p className="m-5 p-3">
                This is the Wrong URL please enter valid keyword
      </p>
        </div>
    );
}

const Form = () => {
    return (
        <div>
            <h6>(day-12 form)</h6>
            <Navbar bg="dark" variant="dark" className="p-3">
                <nav>
                    <Link to='/' className='ml-5 linkstyle'>Home</Link>
                    <Link to='/contact' className='ml-5 linkstyle'>Contact</Link>
                    <Link to='/company' className='ml-5 linkstyle'>Company</Link>
                </nav>
            </Navbar>
            <Switch>
                <Route exact path="/"> <HomePage /></Route>
                <Route path="/contact"> <ContactUs /></Route>
                <Route path="/company"><AboutUs /></Route>
                <Route path="*"><NotFound /></Route>
            </Switch>
        </div>
    );
};

export default Form;
